<template>
    <form class="login-page" @submit.prevent="checkForm">
        <div class="centered">
            <input v-model="username" type="text" name="username" id="username" placeholder="Username">
            <input v-model="password" type="password" name="password" id="password" placeholder="Password">
        </div>
        <div class="bottomed">
            <input type="submit" value="Log in">
        </div>
    </form>
</template>

<script>

import {LibrusService} from '../LibrusService.js';

const ls = new LibrusService();

export default {
    data(){
        return {
            username:null,
            password:null,
            token:null
        }
    },

    methods:{
        checkForm(){
            if(this.username && this.password){
                ls.getToken(this.username, this.password).then(
                    r => {
                        this.token = r.data.result;
                    }
                ).catch(
                    e => {
                        this.token = e.response.data.result;
                    }
                )
            }
        }
    },

    created() {
        this.$parent.nav.title = 'Log in';
    }
}
</script>

<style lang='scss' scoped>

.login-page{
    height: calc(100% - 5rem); //minus navbar height
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2rem 16rem 1fr 4rem;
    .centered{
        grid-row: 2;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
    }
    .bottomed{
        grid-row: 4;
    }
}

</style>